import { useState } from 'react'

import TradeInfo from './TradeInfo';
import Position from './Position';
import History from './History';

export default function Lite(){
  const [curTab, setCurTab] = useState('tradeInfo');

  const switchTab = current => setCurTab(current);

  return (
    <div className='trade-position'>
      <div className='header'>
        <span className='trade action' onClick={() => switchTab('tradeInfo')}>
          TRADE
        </span>
        <span
          className='position action' onClick={() => switchTab('position')}>
          MY POSITION
        </span>
        <span className='historynaction' onClick={() => switchTab('history')}>
          HISTORY
        </span>
      </div>
      {curTab === 'tradeInfo' && <TradeInfo/>}
      {curTab === 'position' && <Position/>}
      {curTab === 'history' && <History/>}
  </div> 
  )
}